<template>
  <div class="midr">
    <div class="midr1 mid1">
      <span :class="{active: show === 'yjMine'}"><a href="javascript:;" @click="showTab('yjMine')"></a></span>
      <span :class="{active: show === 'yjAdd'}"><a href="javascript:;" @click="showTab('yjAdd')"></a></span>
    </div>
    <!--  -->
    <div class="midr2 content" v-if=" show === 'yjMine' ">
      <div class="cell" v-for="(yj, index) in youji">
        <!--<router-link :to="{ path: 'show/youjiShow' }">-->
        <router-link :to="{path: '/show/youjiShow', query: { id: yj.yjId }}" target="_blank">
          <img :src="yj.yjImg">
          <p class="title" v-text="yj.yjName"></p>
        </router-link>
        <p class="time" v-text="yj.yjTime"><span><a href="##"></a></span></p>
      </div>
    </div>
    <!--  -->
    <div class="midr3 content" v-if=" show === 'yjAdd' ">
      <form action="##" class="form">
        <p class="formtr formtrp">
          <span></span>
          <input type="text" class="text-input" style="width: 500px;" v-model="yjAdd.title">
        </p>
        <p class="formtr formtrp">
          <span></span>
          <select name="" id="location1" class="select" style="width: 96px; margin-right:5px;">
            <option value="1"></option>
          </select>
          <select name="" id="location2" class="select" style="width: 96px; margin-right:5px;">
            <option value="1"></option>
          </select>
          <select name="" id="location3" class="select" style="width: 96px; margin-right:5px;">
            <option value="1"></option>
          </select>
          <select autocomplete="off" name="" id="location4" class="select" style="width: 110px; margin-right:5px;" v-model="yjAdd.lishuxian">
            <option value=""></option>
            <option value=""></option>
            <option value=""></option>
            <option value=""></option>
            <option value=""></option>
            <option value=""></option>
            <option value=""></option>
            <option value=""></option>
            <option value=""></option>
            <option value=""></option>
            <option value=""></option>
            <option value=""></option>
            <option value=""></option>
            <option value=""></option>
            <option value=""></option>
          </select>
          <select name="" id="location5" class="select" style="width: 110px" v-model="yjAdd.lishuxiang">
            <option value=""></option>
            <option value=""></option>
            <option value=""></option>
            <option value=""></option>
            <option value=""></option>
            <option value=""></option>
            <option value=""></option>
            <option value=""></option>
          </select>
        </p>
        <div class="formtr formtrDiv clear">
          <span></span>
          <div class="xc_cover"></div>
          <span>&nbsp;</span>
          <a href="" class="a-file-btn-input">
            <w></w>
            <input type="file" class="file-btn-input" id="" multiple="multiple" @change="setImagePreviews1('file-btn-input','xc_cover',0)">
          </a>
        </div>
        <div class="formtr formtrDiv">
          <span></span>
          <div class="vueEditor">
            <vue-html5-editor @change="updateData" :content="content" :height="500" :z-index="1000" :auto-height="true"></vue-html5-editor>
          </div>
          <!-- <textarea name="" id="" cols="80" rows="30"></textarea> -->
        </div>
        <p class="btnbar">
          <span></span>
          <input type="button" value="" class="btn" @click="sendMessage">
          <router-link :to="{path: '/show/youjiShow2', query: { title: yjAdd.title, lishuxian: yjAdd.lishuxian, lishuxiang: yjAdd.lishuxiang, fengmian: yjAdd.fengmian, zhengwen: yjAdd.zhengwen, author: userInfo.userName }}" class="btn" target="_blank">
            
          </router-link>
          <input type="reset" value="" class="btn btn-gray">
        </p>
      </form>
    </div>
    <div style="clear:both"></div>
  </div>
</template>

<script>
  import axios from 'axios'
  export default {
    data () {
      return {
        show: 'yjMine',
        content: 'hello',
        youji: [
          {
            yjId: 1,
            yjName: ' ',
            yjTime: '2017/7/2',
            yjImg: '/static/special1.jpg'
          },
          {
            yjId: 2,
            yjName: '',
            yjTime: '2017/7/2',
            yjImg: '/static/special3.jpg'
          }
        ],
        yjAdd: {
          title: '',
          lishuxian: '',
          lishuxiang: '',
          fengmian: '',
          zhengwen: ''
        },
        sendData: {
          name: '',
          location: '',
          mainpic: '',
          content: ''
        }
      }
    },
    props: ['userInfo'],
    methods: {
      showTab (str) {
        this.show = str
      },
      updateData (data) {
        this.yjAdd.zhengwen = data
      },
      sendMessage () {
        this.sendData.name = this.yjAdd.title
        this.sendData.location = '' + this.yjAdd.lishuxian + this.yjAdd.lishuxiang
        this.sendData.mainpic = this.yjAdd.fengmian
        this.sendData.content = this.yjAdd.zhengwen
        console.log(' { : ' + this.sendData.name + ' ,: ' + this.sendData.location + ' ,: ' + this.sendData.mainpic + ' , ' + this.sendData.content + ' }')
        axios.get('http://127.0.0.1:8000/ssm01/userarticle/newarticle2', this.sendData).then(function (res) {
          if (res.data === '') {
            window.alert('')
          } else {
            window.alert('')
          }
        }).catch(function (err) {
          console.log(err)
        })
      },
      getData () {
        axios.get('http://127.0.0.1:8000/ssm01/userarticle/UserArticles').then(function (res) {
          this.youji = res.data
        }).catch(function (err) {
          console.log('' + err)
        })
      },
      // 
      setImagePreviews1 (class1, class2, index) {
        var choosePic = document.getElementsByClassName(class1)[index]
        var showPic = document.getElementsByClassName(class2)[index]
        showPic.innerHTML = ''
        var fileList = choosePic.files
        for (var i = 0; i < fileList.length; i++) {
          showPic.innerHTML += "<img class='img" + class2 + i + "' style='display:block;width:100%;height:100%;' />"
          var imgObjPreview = document.getElementsByClassName('img' + class2 + i)[index]
          imgObjPreview.src = window.URL.createObjectURL(choosePic.files[i])
          this.yjAdd.fengmian = imgObjPreview.src
        }
      }
    }
  }
</script>

<style scoped>
  @import "../../styles/style.css";

  .midr .midr2 .cell {
    width: 25%;
    height: 260px;
    float: left;
    padding: 20px 20px 0px 0px;
    box-sizing: border-box;
  }

  .midr .midr2 .cell img {
    width: 100%;
    height: 180px;
  }

  .midr .midr2 .cell .title {
    width: 95%;
    height: 30px;
    line-height: 30px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    padding-left: 5px;
  }

  .midr .midr2 .cell .time {
    width: 100%;
    height: 30px;
    line-height: 30px;
    padding-left: 5px;
    font-size: small;
  }

  .midr .midr2 .cell .time span a {
    color: #ccc;
    display: inline-block;
    float: right;
    font-size: small;
  }
</style>




