<template>
  <div class="main">
    <!--  -->
    <div class="add_title">
      
    </div>
    <div class="hengg1"></div>
    <!--  -->
    <form class="clear form">
      <p class="formtr formtrp">
        <span></span>
        <input type="text" class="text-input input-long" v-model="xcName">
      </p>
      <div class="formtr formtrDiv">
        <span></span>
        <textarea class="textarea" rows="13" v-model="xcIntroduction"></textarea>
      </div>
      <p class="formtr formtrp">
        <span></span>
        <select name="" id="location1" class="select" style="width: 110px; margin-right:10px;">
          <option value="1"></option>
          <option value="0">-</option>
        </select>
        <select name="" id="location2" class="select" style="width: 110px; margin-right:10px;">
          <option value="1"></option>
          <option value="0">-</option>
        </select>
        <select name="" id="location3" class="select" style="width: 110px; margin-right:10px;">
          <option value="1"></option>
          <option value="0">-</option>
        </select>
        <select name="" id="location4" class="select" style="width: 110px; margin-right:10px;">
          <option value="0">-</option>
          <option value="1"></option>
          <option value="2"></option>
          <option value="3"></option>
          <option value="4"></option>
          <option value="5"></option>
          <option value="6"></option>
          <option value="7"></option>
          <option value="8"></option>
          <option value="9"></option>
          <option value="10"></option>
          <option value="11"></option>
          <option value="12"></option>
          <option value="13"></option>
          <option value="14"></option>
          <option value="15"></option>
        </select>
      </p>
      <div class="formtr formtrDiv clear">
        <span></span>
        <div class="xc_cover"></div>
        <span>&nbsp;</span>
        <a href="" class="a-file-btn-input">
          <w></w>
          <input type="file" class="file-btn-input" id="" multiple="multiple" @change="setImagePreviews1('file-btn-input','xc_cover',0)">
        </a>
      </div>

      <!-- 	 -->
      <div class="choose_type">
        <ul>
          <li :class="{ activeXC: showStr === 'techan' }" @click="showTab('techan')"></li>
          <li :class="{ activeXC: showStr === 'meijing' }" @click="showTab('meijing')"></li>
          <li :class="{ activeXC: showStr === 'wenhua' }" @click="showTab('wenhua')"></li>
          <li :class="{ activeXC: showStr === 'huodong' }" @click="showTab('huodong')"></li>
        </ul>
      </div>
      <!--  -->
      <div class="special clear" v-if=" showStr === 'techan' ">
        <ul>
          <li class="special_ed"  v-for="(oneTC, index) in tcList" :key="index">
            <div class="show_spe"></div>
            <input type="text" size="10" class="input_spe" v-model="oneTC.tcName">
            <div>
              <a href="" class="a-file-btn-input">
                <w></w>
                <input type="file" class="file-btn-input cho_spe" multiple="multiple" @change="setImagePreviews2('cho_spe','show_spe',index)">
              </a>
              <input type="button" class="delete_spe" value="" @click="removeTC(index)"/>
            </div>
          </li>
          <!-- <li class="special_ed">
            <img src="../../images/special1.jpg">
            <input type="text" size="10">
            <div>
              <button></button>
              <button></button>
            </div>
          </li> -->
          <li class="special_ing">
            <img class="add_tc" src="../../icons/add.png" @click="addNewTC" />
          </li>
        </ul>
      </div>
      <!--  -->
      <div class="sight clear" v-if=" showStr === 'meijing' ">
        <ul>
          <li class="sight_pic">
            <div >
              <a href="" class="a-file-btn-input btn-grayd">
                <w></w>
                <input type="file" class="file-btn-input " multiple="multiple">
              </a>
            </div>
          </li>
          <li class="sight_pic">
            <div >
              <a href="" class="a-file-btn-input">
                <w></w>
                <input type="file" class="file-btn-input" multiple="multiple">
              </a>
            </div>
          </li>
        </ul>
      </div>
      <!--  -->
      <div class="editor clear"  v-if=" showStr === 'wenhua' ">
        <vue-html5-editor @change="updateData1" :content="wenhua" :height="500" :z-index="1000" :auto-height="true" ></vue-html5-editor>
      </div>
      <!--  -->
      <div class="editor clear"  v-if=" showStr === 'huodong' ">
        <vue-html5-editor @change="updateData2" :content="huodong" :height="500" :z-index="1000" :auto-height="true" ></vue-html5-editor>
      </div>
      <p class="btnbar">
        <span></span>
        <input type="submit" value="" class="btn">
        <input type="button" value="" class="btn">
        <input type="reset" value="" class="btn btn-gray">
      </p>
    </form>
  </div>
</template>

<script>
  export default {
    data () {
      return {
        showStr: 'techan',
        wenhua: 'wenhua',
        huodong: '',
        xcName: '',
        xcIntroduction: '',
        xcCover: '',
        newTC: {
          imgSrc: '',
          tcName: ''
        },
        tcList: [
          {
            imgSrc: '',
            tcName: ''
          }
        ]
      }
    },
    created () {
      this.menun()
    },
    methods: {
      showTab (str) {
        this.showStr = str
      },
      updateData1 (data) {
        this.wenhua = data
        console.log(data)
      },
      updateData2 (data) {
        this.huodong = data
        console.log(data)
      },
      menun () {
        window.scrollTo(0, 0)
      },
      // 
      setImagePreviews1 (class1, class2, index) {
        var choosePic = document.getElementsByClassName(class1)[index]
        var showPic = document.getElementsByClassName(class2)[index]
        showPic.innerHTML = ''
        var fileList = choosePic.files
        for (var i = 0; i < fileList.length; i++) {
          showPic.innerHTML += "<img class='img" + class2 + i + "' style='display:block;width:100%;height:100%;' />"
          var imgObjPreview = document.getElementsByClassName('img' + class2 + i)[index]
          imgObjPreview.src = window.URL.createObjectURL(choosePic.files[i])
          this.xcCover = imgObjPreview.src
        }
      },
      // 
      setImagePreviews2 (class1, class2, index) {
        var choosePic = document.getElementsByClassName(class1)[index]
        var showPic = document.getElementsByClassName(class2)[index]
        showPic.innerHTML = ''
        var fileList = choosePic.files
        // window.alert(fileList.length)
        for (var i = 0; i < fileList.length; i++) {
          showPic.innerHTML += "<img class='img" + class2 + i + "' style='display:block;width:100%;height:100%;' />"
          // :src='" + imgsrc + "'
          var imgObjPreview = document.getElementsByClassName('img' + class2 + i)[index]
          imgObjPreview.src = window.URL.createObjectURL(choosePic.files[i])
          this.tcList[index].imgSrc = imgObjPreview.src
          console.log(showPic.innerHTML)
          console.log(this.tcList[index].imgSrc)
        }
      },
      // 
      addNewTC () {
        this.tcList.push(JSON.parse(JSON.stringify(this.newTC)))
      },
      removeTC (index) {
        this.tcList.splice(index, 1)
      }
    }
  }
</script>

<style scoped>
  @import "../../styles/style.css";
  .editor{
    width: 90%;
    margin: 0 auto;
  }
  .specialCell img{
    width:80%;
    margin-right: 0 auto;
  }
  .activeXC{
    color:#FFDE3A;
    border-top:3px solid #FFDE3A;
  }
  .main{
    /*height: 800px;*/
    width: 1000px;
    margin: 0px auto;
    /*background: rgba(55,220,188,0.7);*/
    /*box-shadow: 0px -4px 3px -3px #FFDE3A;*/
  }
  .hengg1{
    height: 2px;
    /* width: 100%;
    margin: 0px auto 40px; */
    width: 1200px;
    margin: 0px -100px 40px;
    background: #ccc;
  }
  /**/
  .add_title{
    width: 100%;
    height: 100px;
    line-height: 100px;
    text-align: center;
    font-size: 24px;
    letter-spacing: 10px;
  }

/**/
  .choose_type{
    height: 65px;
    width: 100%;
  }
  .choose_type ul{
    border-top: 2px solid #ccc;
    width: 100%;
  }
  .choose_type ul li{
    height: 65px;
    line-height: 50px;
    margin-left: 30px;
    float: left;
    cursor: pointer;
    margin-top: -2px;
    padding: 0 5px;
  }
/**/
  .add_tc{
    margin: 125px 88px;
    cursor: pointer;
  }
  .special{
    width: 100%;
  }
  .special ul{
    width: 98%;
    margin-left: 2%;
  }
  .special_ed{
    width: 23%;
    margin: 0px 2% 20px 0px;
    height: 300px;
    float: left;
  }
  .special_ing{
    width: 22.5%;
    margin: 0px 2% 20px 0px;
    height: 300px;
    float: left;
  }
  .show_spe{
    width: 100%;
    height: 200px;
    background: #f2f2f2;
    border-radius: 3px;
  }
  .input_spe{
    height: 30px;
    width: 100%;
    margin: 15px 0px;
    border: 1px solid #ccc;
    border-radius: 3px;
    box-sizing: border-box;
  }
  .special_ing{
    border: 2px solid #f2f2f2;
    border-radius: 4px;
  }
  .delete_spe{
    padding: 5px 10px;
    background: #F2F2F2;
    border-radius: 3px;
    margin-left: 65px;
    border: 2px solid #F2F2F2;
    width: 70px;
    font-size: small;
    height: 32px;
    font-family: ''; 
    cursor: pointer;
  }
/**/
  .sight{
    width: 100%;
  }
  .sight ul{
    width: 98%;
    margin-left: 2%;
  }
  .sight_pic div{
    width: 90px;
    height: 28px;
    margin: 136px auto;
  }
  .sight_pic,.sight_vid{
    width: 23%;
    margin: 0px 2% 20px 0px;
    height: 300px;
    float: left;
    border: 2px solid #f2f2f2;
    border-radius: 4px;
  }
  .sight_pic button,.sight_vid button{
    width: 43%;
    margin: 129px 30%;
    height: 40px;
    line-height: 34px;
    text-align: center;
    background: #f2f2f2;
    border-radius: 3px;
    border: 1px solid #f2f2f2;
  }

</style>



